.addtodo {
    width: 100%;
    margin-top: 10px;
    height: 50px;
}

.addtodo input {
    box-sizing: border-box;
    border: 1px solid black;
    border-radius: 7px;
    padding: 0.5em;
    width: 80%;
    font-size: large;
}

.addtodo button {
    box-sizing: border-box;
    border: none;
    margin-left: 2em;
    border-radius: 10px;
    background-color: rgb(186, 135, 233);
    width: 10%;
    height: 30px;
    cursor: pointer;
}

.addtodo button:hover {
    background-color: olive;
}
